// import logger from "@docusaurus/logger";
import { NextResponse } from "next/server";

export default async function handleJSessionId(request) {
  //  JSESSIONID
  const jsessionId = request.cookies.get("JSESSIONID");
  // 
  const response = NextResponse.next();
  //  CSRF Token
  //  JSESSIONID Cookie
  if (jsessionId) {
    // logger.success(`yes `);
    //  cookie
    //@ts-ignore
    response.cookies.set("JSESSIONID", jsessionId.value, {
      httpOnly: true, //  HTTP-only JavaScript 
      // secure: true, //  HTTPS 
      sameSite: "Lax", // 
      path: "/",
    });
  } else {
    console.warn(`no credial`);
  }

  return response;
}
